  .btn-common {
    background: #FF0083;
border-radius: 8px;
    margin: 10px 0;
    padding: 10px 15px;
    color: #fff;
    text-decoration: none;
    border: none;
    font-size: 13px;
    text-transform: uppercase;
    font-family: 'Gellix', serif;

    &:hover {
     color: #FF0083;
     box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
     background-color: #fff;
   }
   &:active {
    color: #FF0083;
     box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
     background-color: #fff;
   }
   &:focus {
     outline: none;
   }
}


